export default {
    data() {
        return {
            phone: '',
            shade: false,
            categart: ''
        };
    },
    onShow() {
        var _this = this;
        uni.request({
            url: this.url + 'home/',
            method: 'GET',
            header: {
                Authorization: 'JWT' + ' ' + this.global_.token
            },
            success(res) {
                _this.categart = res.data.data.categart;
            }
        });
    },
    onLoad() {
        var p = this.global_.phone;
        var p1 = p.split(''); //字符串转为数组
        var phone =
            p1
                .slice(0, 3)
                .join()
                .replace(/,/g, '') +
            '****' +
            p1
                .slice(7, 11)
                .join()
                .replace(/,/g, '');
        this.phone = phone;
    },
    onHide() {
        this.shade = false;
    },
    methods: {
        cancel: function() {
            this.shade = false;
        },
        sure: function() {
            this.global_.token == '';
            uni.removeStorageSync('phone');
            uni.removeStorageSync('token');
            uni.reLaunch({
                url: '../login/login'
            });
        },
        power: function() {
            uni.navigateTo({
                url: '../../my/power/power'
            });
        },
        personal: function() {
            uni.navigateTo({
                url: '../../my/personal/personal?phone=' + this.phone
            });
        },
        wallet: function() {
            uni.navigateTo({
                url: '../../my/my-wallet/my-wallet'
            });
        },
        trans: function() {
            uni.navigateTo({
                url: '../mill/mill'
            });
        },
        machinsale: function() {
            uni.navigateTo({
                url: '../../my/machine-sale/machine-sale'
            });
        },
        address: function() {
            uni.request({
                url: this.url + 'walletaddress/',
                method: 'GET',
                header: {
                    Authorization: 'JWT' + ' ' + this.global_.token
                },
                success(res) {
                    if (res.statusCode == 400) {
                        uni.showToast({
                            title: '用户未实名认证',
                            icon: 'none',
                            duration: 2000
                        });
                    }
                    if (res.statusCode == 201) {
                        uni.showToast({
                            title: '身份认证审核中，请等待',
                            icon: 'none',
                            duration: 2000
                        });
                    }
                    if (res.statusCode == 200) {
                        uni.navigateTo({
                            url: '../../my/address/address'
                        });
                    }
                    if (res.statusCode == 302) {
                        uni.showToast({
                            title: '用户未设置交易密码',
                            icon: 'none',
                            duration: 2000
                        });
                    }
                }
            });
        },
        tradePassword: function() {
            var that = this;
            uni.request({
                url: this.url + 'setmoney/',
                method: 'GET',
                header: {
                    Authorization: 'JWT' + ' ' + this.global_.token
                },
                success(res) {
                    if (res.statusCode == 302) {
                        uni.showToast({
                            title: '用户未绑定邮箱',
                            icon: 'none',
                            duration: 2000
                        });
                        return false;
                    }
                    if (res.statusCode == 400) {
                        uni.navigateTo({
                            url: '../../my/change-password/change-password'
                        });
                    }
                    if (res.statusCode == 200) {
                        uni.navigateTo({
                            url: '../../my/trade-password/trade-password'
                        });
                    }
                }
            });
        },
        loginPassword: function() {
            uni.request({
                url: this.url + 'updataloginpassword/',
                method: 'GET',
                header: {
                    Authorization: 'JWT' + ' ' + this.global_.token
                },
                success(res) {
                    if (res.statusCode == 400) {
                        uni.navigateTo({
                            url: '../../my/change-loginPassword/change-loginPassword'
                        });
                    }
                    if (res.statusCode == 200) {
                        uni.navigateTo({
                            url: '../../my/change-pass/change-pass'
                        });
                    }
                }
            });
        },
        bindEmail: function() {
            uni.request({
                url: this.url + 'linkemail/',
                method: 'GET',
                header: {
                    Authorization: 'JWT' + ' ' + this.global_.token
                },
                success(res) {
                    if (res.statusCode == 200 || res.statusCode == 201) {
                        uni.navigateTo({
                            url: '../../my/email/email'
                        });
                    }
                    if (res.statusCode == 400) {
                        uni.navigateTo({
                            url: '../../my/unbindemail/unbindemail'
                        });
                    }
                }
            });
        },
        suggest: function() {
            uni.navigateTo({
                url: '../../my/suggest/suggest'
            });
        },
        help: function() {
            uni.showToast({
                title: '暂未开放',
                icon: 'none',
                duration: 2000
            });
        },
        certification: function() {
            uni.request({
                url: this.url + 'realname/',
                method: 'GET',
                header: {
                    Authorization: 'JWT' + ' ' + this.global_.token
                },
                success: function(res) {
                    if (res.statusCode == 202) {
                        uni.showToast({
                            title: '已实名认证',
                            icon: 'none',
                            duration: 2000
                        });
                        return false;
                    }
                    if (res.statusCode == 205) {
                        uni.showToast({
                            title: '身份认证审核中，请等待',
                            icon: 'none',
                            duration: 2000
                        });
                        return false;
                    }
                    if (res.statusCode == 200) {
                        uni.navigateTo({
                            url: '../../my/identity/identity'
                        });
                    }
                }
            });
        },
        mymachine: function() {
            uni.navigateTo({
                url: '../../my/my-machine/my-machine'
            });
        },
        logout: function() {
            this.shade = true;
        },
        coupon: function() {
            uni.navigateTo({
                url: '../../my/coupon/coupon',
                success: res => {},
                fail: () => {},
                complete: () => {}
            });
        },
        set: function() {
            uni.navigateTo({
                url: '../set/set'
            });
        }
    }
};
